<template>
	<div class="home">
		<div class="flex-justify-spaceBetween border-bottom_D">
			<div class="home_list">
				<div v-for="(item,index) in list" :key="index" :class="listindex==index ? 'color-blue' : '' "
					class="home_list_item cursor-pointer" @click="handClick(index)">
					{{item}}
				</div>
			</div>
			<div class="home_but">
				<div class="basicSetuUp_item_button cursor-pointer" @click="getAuthLink">
					一键授权
				</div>
			</div>
		</div>
		
		<div class="home_box" v-if="appList.length">
			<div v-for="(item,index) in appList" :key="index" class="home_box_item" @click="handGoBasic(item.wxAppId)">
				<div class="flex-justify-spaceBetween pad-23-20 border-bottom_D">
					<div class="word-overstep-hide" style="width: 150px;">{{item.name}}</div>
					<div>
						<div class="basicSetuUp_item_button cursor-pointer BGcolor_ff0095 font-size10" v-if="industryList[item.industry] == '婚纱'"> {{ industryList[item.industry]}}</div>
						<div class="basicSetuUp_item_button cursor-pointer BGcolor_5f9ea0 font-size10" v-if="industryList[item.industry] == '古装'"> {{ industryList[item.industry]}}</div>
						<div class="basicSetuUp_item_button cursor-pointer font-size10" v-if="industryList[item.industry] == '儿童'"> {{ industryList[item.industry]}}</div>
					</div>
				</div>
				<div class="item_ul border-bottom_D">
					<ul>
						<li>
							<div>建立时间</div>
							<div>{{item.creationTime}}</div>
						</li>
						<li>
							<div>累计消息</div>
							<div>{{item.normalNum}}条</div>
						</li>
						<li>
							<div>异常消息</div>
							<div>{{item.exceptNum}}条</div>
						</li>
					</ul>
				</div>

				<div class="item_but">
					<div class="_but" @click="handGoBasic(item.wxAppId)">立即查看</div>
				</div>
			</div>
		</div>
		<div class="home_kong" v-else>
			<div>
				<div>
					<img src="../assets/images/zanwu.png" width="320" height="165" />
				</div>
				<div class="home_kong_title">暂无数据</div>
				<!-- <div>
					<Button type="info">一键授权</Button>
				</div> -->
			</div>
		</div>

		<!-- <div class="home_kong" v-else>
			<div>
				<div>
					<img src="../assets/images/kongzhuangtai.png" />
				</div>
				<div class="home_kong_title">您还未授权哦</div>
				<div>
					<Button type="info">一键授权</Button>
				</div>
			</div>
		</div> -->

	</div>
</template>

<script>
	import {
		indexApiModules
	} from '@/api';
	import util from '@/utils/util.js';
	export default {
		name: "Home",
		data() {
			return {
				industryList: {
					1: '婚纱',
					2: '儿童',
					3: '古装'
				},
				// list: ['微信公众号', '微信小程序', '抖音企业号'],
				list: ['微信公众号'],
				listindex: 0,
				appList: [],
			}
		},
		created() {},
		mounted() {
			// console.log('这里是测试拦截器的')
			this.getUserCompanyList()

		},
		methods: {
			// 获取企业id
			getUserCompanyList() {
				indexApiModules.getInitGetusercompanylist({}).then(res => {
					let ccId = ''
					if (process.env.NODE_ENV === "development") {
						ccId = '00000000737f5b5a01737fbfce600000'
					} else {
						ccId = util.getCookie('ccId')
					}
					console.log('ccid默认值', ccId);
					
					// 正式 // 如果有ccId
					if(ccId){
						// 拿ccid去匹配,
						var findVal = res.data.find(item => { return item.ccId === ccId; });
						if (findVal) {
							// 有则储存
							this.$cookies.set('companyId', findVal.companyId)
							this.$cookies.set('ccId', findVal.ccId)
							this.loanDetailed()
							this.handloanDetailed()
						}else{
							this.$router.push({path: '/opening'})
						}
					}else{
						// 如果没有ccid，那么取第一个
						if(res.data){
							let findVal = res.data[0]
							this.$cookies.set('companyId', findVal.companyId)
							this.$cookies.set('ccId', findVal.ccId)
							this.loanDetailed()
							this.handloanDetailed()
						}else{
							window.location.href = process.env.NODE_ENV == "production" ? 'https://i.lyfz.net/':'https://i.27yn.cn/'
						}

					}				
				})
			},
			
			loanDetailed() {
				indexApiModules.getInitBase({
					companyId: this.$cookies.get('companyId')
				}).then(res => {
					// 把用户信息存入本地缓存中
					localStorage.setItem('userInfo', JSON.stringify(res.data))
					console.log('这里是返回成功了', res)
				})
			},

			handloanDetailed() {
				let type = this.listindex + 1
				indexApiModules.companyGetlist({
					type: type
				}).then(res => {
					console.log('', res)
					this.appList = res.data
				})
			},
			handClick(index) {
				this.listindex = index
				this.handloanDetailed()
			},
			// 获取授权链接
			getAuthLink() {
				let api = process.env.VUE_APP_BASE_API
				let url = 'other/getAuthLink'
				let wxAppId = localStorage.getItem('wxAppId')
				let apiurl = api + url + '?wxAppId=' + wxAppId
				console.log(apiurl);
				window.open(apiurl, "_blank");
				// indexApiModules.companyGetAuthLink({}).then(res => {
				// 	let url = res.data.url
				// 	window.open(url, '_blank');
				// })
			},

			handGoBasic(row) {
				localStorage.setItem('wxAppId', row)
				this.$router.push({
					path: '/BasicSetuUp'
				})
				localStorage.setItem('headTopType', 'home')
			},

		},

	};
</script>

<style scoped lang="less">
	.home {
		width: 100%;
		height: calc(100vh - 100px);
		// overflow-y: hidden;
		.home_list {
			display: flex;
			align-items: center;
			padding: 14px 16px;

			.home_list_item {
				margin-right: 30px;
				color: #333333;
				font-size: 16px;
			}

			.color-blue {
				color: #2d8cf0;
			}
		}
		.home_but {
			margin-right: 16px;
		}
		



		.home_box .home_box_item:nth-child(4n) {
			margin-right: 0;
		}
		.home_box .home_box_item:hover{
			 border: 1px solid #2d8cf0;
			 
		}

		.home_box {
			// padding: 20px 46px;
			padding:30px;
			position: relative;
			overflow: auto;
			height: calc(100% - 53px);
		}
		// 自定义滚动条样式// 自定义滚动条
		.home_box::-webkit-scrollbar {
			/*高宽分别对应横竖滚动条的尺寸*/
			width: 5px;    
			height: 5px;
		}
		.home_box::-webkit-scrollbar-thumb {
			/*滚动条里面小方块*/
		   background-color:#f0f0f0;
		   -webkit-border-radius: 2em;
		   -moz-border-radius: 2em;
		   border-radius:2em;
		}
		.home_box::-webkit-scrollbar-thumb:hover { /* 鼠标移入滑块 */
		    background-color: rgba(0,0,0,.3);
		}
		.home_box::-webkit-scrollbar-track {
			/*滚动条里面轨道*/
			   background-color:#fff;
			   -webkit-border-radius: 2em;
			   -moz-border-radius: 2em;
			   border-radius:2em;
		}
		
		
		
		
		
		

		.home_box_item {
			width: 240px;
			height: 240px;
			border-radius: 8px;
			border: 1px solid #DDDDDD;
			margin-right: 58px;
			margin-bottom: 15px;
			cursor: pointer;
			position: relative;
			float: left;
			-webkit-box-sizing: border-box;
			box-sizing: border-box;
			.home_box_item_name{
				font-size: 16px;
				color: #333333;
			}

			.pad-23-20 {
				padding: 13px 10px;
			}

			.item_ul {
				padding: 13px 10px;
				color: #666666;
				font-size: 13px;

				ul li {
					margin-bottom: 15px;
					display: flex;
					justify-content: space-between;
					align-items: center;

					div:nth-child(2) {
						width: 130px;
					}
				}
			}

			.item_but {
				padding: 13px 10px;
				display: flex;
				justify-content: center;
				align-items: center;
				._but {
					padding: 4px 20px;
					// background: #2d8cf0;
					border-radius: 4px;
					color: #2d8cf0;
					border: 1px solid #2d8cf0;
		}
			}
		}

		.home_kong {
			// padding: 20px;
			// position: relative;
			// overflow: auto;
			// height: calc(100% - 40px);
			text-align: center;
			margin-top: 200px;
			.home_kong_title {
				font-size: 24px;
				margin: 20px 0;
				font-weight: 400;
				color: #666666;
			}
		}

		.basicSetuUp_item_button {
			padding: 4px 15px;
			background: #2d8cf0;
			border-radius: 4px;
			color: #fff;
			font-size: 14px;
		}
	}
</style>
